/**
 *  ,     
 * @property level -  ,  1  4
 * @property attack -  
 * @property defence -  
 * @property health -  
 * @property type -      :
 * swordsman
 * bowman
 * magician
 * daemon
 * undead
 * vampire
 */
export default class Character {
  constructor(level, type) {
    if (new.target === Character) {
      throw new Error('Character is abstract');
    }
    this.level = level;
    this.type = type;
    this.attack = 0;
    this.defence = 0;
    this.health = 100;
  }
}
